nav {
    height: 4em;
    width: 100%;
    margin: 0;
    top: 0;
    color: rgba(0, 0, 0, 0.548);
    background-color: white;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
}

.fixed-nav ul {
    height: 100%;
    list-style: none;
    display: -webkit-flex;
    display: flex;
    margin: 0;
    padding: 0;
}

.fixed-nav li {
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    transition: all 0.3s ease-in;
}

.fixed-nav li:not(.main-button) {
    cursor: pointer;
}

.fixed-nav li.main-button {
    padding-left: 10px;
    -webkit-flex-grow: 5;
            flex-grow: 5;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}

.fixed-nav li:not(.main-button):hover {
    border-bottom: 3px solid #5ac8df;
    transition: all 0.1s ease-in;
}

.fixed-nav {
    display: block;
    position: fixed;
    z-index: 1;
}

.fixed-nav .navbar-logo {
    height: 100%;
    cursor: pointer;
}
